<template>
  <div class="userAssets" v-loading="load">

    <div class=" flex align between">
      <div class="menmuTitle flex align between">
        <h1 class="hasBack flex align"><i class="el-icon-back hoverTr3" @click="goback()" title="返回"></i> 释放记录</h1>
      </div>
      <!-- menmuTitle -->
    </div>

    <div class="space20"></div>

    <div class="listItem" v-if="tableData.length">
      <table>
        <thead>
          <tr>
            <th class="textLeft">操作</th>
            <th class="textLeft">币种</th>
            <th class="textCenter">金额</th>
            <th class="textRight">日期</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,index) in tableData" :key="index">
            <td class="textLeft">{{setTypeText(item.type)}}</td>
            <td class="textLeft">{{getCoinName(item.coinId)}}</td>
            <td class="textCenter"><b :class="item.amount>0?'color-red':'color-green'">{{item.amount>0?`+${item.amount}`:item.amount}}</b></td>
            <td class="textRight">{{item.addTime}}</td>
          </tr>
        </tbody>
      </table>

    </div>
    <!-- assetsTable -->

    

     <div v-if="tableData.length==0" class="nullData"><span>暂无数据</span></div>

    <div class="textRight" v-if="pageInfo && pageInfo.total>0">
      <div class="space40"></div>
      <el-pagination
        background
        layout="prev, pager, next"
        @current-change="x=>query.pageNum = x"
        :page-size="pageInfo.pageSize"
        :total="pageInfo.total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import {
    orderIncomeList as getList,
    logType
  } from '../data/apis';

  export default {
    data() {
      return {
        load:false,
        
        tableData:[],//表格数据
        actionsOptions: [],//操作选择
        query:{
          pageSize:20,
          pageNum:1,
          type:'',
          orderId:'',
          startTime:'',
          endTime:''
        },
        pageInfo:''//分页信息
      }
    },
    computed: {
      ...mapState(['userInfo']),
      coinOptions(){
        return this.userInfo.balanceModels.map(v=>{
          return {value:v.coin.id,label:v.coin.coinName}
        })
      }//coinOptions
    },
    methods: {
      getCoinName(id){//获取币种名称
        return this.userInfo.balanceModels.find(x=>x.coin.id==id).coin.coinName;
      },//getCoinName
      getData(){//获取数据
        this.dataList = [];
        this.load = true;
        getList(this.query).then(v=>{
          let {list} = v.data;
          this.tableData = list;
          this.pageInfo = v.data;
          this.load = false;
        })
      },//getData
      getType(){//获取类型
      this.load = true;
        logType().then(v=>{
          for (const key in v.data) {
            let item = {value:key,label:v.data[key]}
            this.actionsOptions.push(item);
          }
          this.load = false;
        })
      },//getType
      setTypeText(type){//设置类型文字
        return this.actionsOptions.find(x=>x.value==type).label || '未知操作';
      }//setTypeText
    },
    watch:{

      query:{
        deep:true, 
        handler:function(newV,oldV){
          this.getData();
        }
      }//query

    },//watch
    mounted() {
      this.query.orderId = this.$route.query.id;
      this.getData();
      this.getType();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";

  .hasBack {
    i {
      color: $them_color;
      font-size: 20px;
      margin-right: 20px;
      cursor: pointer;
    }
  }

  .search {
    margin-top: 20px;

    .el-select {
      margin-right: 20px;
    }
  }

</style>



